<template>
    <div class='root'>
        <action-bar title='店铺现货列表'></action-bar>
        <div class="search">
            <div class="search-input">
                <van-icon name="search" color="#b4b4b4" class="search-icon" size="16px" />
                <input class="search-text" v-model="searchStr" placeholder="搜索想要创建的商品" />
            </div>
            <div class="search-btn" @click="clickSearch">搜索</div>
        </div>
        <div class='container'>
            <scroll-view class='content' :data="goodsList" :noMoreData="noMoreData" loadmore @scrolltolower="scrolltolower">
                <goods-item v-for="item in goodsList" :key="item.id">
                    <div slot="imgSlot" class="img-slot" bindtap="handleCheck">
                        <van-checkbox checked-color="#42D18A" class="goods-check" v-model="item.check"
                            icon-size="16px"></van-checkbox>
                        <van-image radius=".25rem" :src="baseUrl + item.imageUrl" class="goods-img" />
                    </div>
                    <div slot="nameSlot">
                        <div>{{ item.productName }}</div>
                    </div>
                    <div slot="attributeSlot">
                        <div class="attribute">
                            <div class="attribute-content">分类：{{ item.categoryName }}</div>
                            <div class="attribute-content">颜色：{{ item.color }}</div>
                        </div>
                        <div class="attribute">
                            <div class="attribute-content">等级：{{ item.level }}</div>
                            <div class="attribute-content">规格：{{ item.unit }}</div>
                        </div>
                    </div>
                </goods-item>
            </scroll-view>
        </div>
        <!-- 操作栏 -->
        <div class="operate">
            <div></div>
            <div class="sure" @click="bindSure">确定</div>
        </div>
    </div>
</template>
<script>

import { Icon, Checkbox } from 'vant'

import goodsItem from '@/components/util/GoodsItem.vue'
import { productSearch } from '@/http/api'
export default {
  name: 'spotGoodsList',
  components: {
    [Icon.name]: Icon,
    [Checkbox.name]: Checkbox,
    goodsItem
  },
  data () {
    return {
      searchStr: '', // 搜索文本内容
      goodsList: [],
      start: 0,
      limit: 10,
      mode: '1', // 1店铺现货商品，2预售商品
      max: -1, // 最多可选几个商品 -1 无限制
      noMoreData: false // 没有更多数据了
    }
  },
  methods: {
    clickSearch () {
      this.goodsList = []
      this.start = 0
      this.getGoodsList()
    },
    bindSure () {
      const goodsList = this.goodsList
      const spotChoiceList = []
      for (const item of goodsList) {
        if (item.check) {
          spotChoiceList.push(item)
        }
      }
      if (spotChoiceList.length === 0) {
        this.$toast('请勾选商品')
        return
      }
      this.$route.params.list = spotChoiceList
      this.$router.back()
    },
    scrolltolower () {
      this.start = this.start += this.limit
      this.getGoodsList()
    },
    getGoodsList () {
      productSearch({
        name: this.searchStr,
        start: this.start,
        limit: this.limit
      }).then(res => {
        let nmd = false
        const result = res.data.result
        if (result.length < 10) {
          nmd = true
        }
        const goodsList = this.goodsList
        goodsList.push(...result)
        this.goodsList = goodsList
        this.noMoreData = nmd
      }).catch(rej => {
        this.$toast(rej.msg)
      })
    }
  },
  computed: {
    baseUrl () {
      return this.$store.state.baseUrl
    }
  },
  mounted () {
    this.getGoodsList()
  }
}
</script>
<style lang="scss" scoped>
.root {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.container {
    flex-grow: 1;
    position: relative;
}

.content {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.search {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 0.5rem;
}

.search-input {
    flex-grow: 1;
    background-color: #f5f7f9;
    border-radius: 0.25rem;
    padding: .1875rem 0.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: cc
}

.search-icon {
    margin-right: 0.5rem;
}

.search-text {
    color: #999;
    font-size: 0.75rem;
    padding: .375rem 0;
    width: 100%;
    border: none;
    background-color: #f5f7f9;
}

.search-btn {
    display: flex;
    align-items: center;
    background-color: white;
    border-radius: .375rem;
    height: 100%;
    font-size: .75rem;
    padding: 0 1.5rem;
    text-align: center;
    border: .0625rem solid #42D18A;
    box-sizing: border-box;
    margin-left: .75rem;
}

.operate {
    width: 100%;
    height: 2.875rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: .375rem .5rem;
    box-sizing: border-box;
}

.sure {
    padding: 0 2rem;
    background-color: #42D18A;
    display: flex;
    align-items: center;
    text-align: center;
    border-radius: .25rem;
    color: white;
    height: 100%;
}

.goods-img {
    width: 20vw;
    height: 20vw;
    background-color: #F2F2F2;
    margin-right: .5rem;
}

.attribute {
    display: flex;
    flex-direction: row;
    font-size: .75rem;
}

.attribute-content {
    width: 50%;
}

.img-slot {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.goods-check {
    padding: .5rem;
}
</style>
